import bannerComponent from '@/components/bannerComponent.vue';
import btnComponent from '@/components/btnComponent.vue';
import listItemComponent from '@/components/listItemComponent.vue';
export default {
	components: {
		bannerComponent,
		btnComponent,
		listItemComponent
	},
	data() {
		return {
			showBottomLoading: true,
			requestCount: 1,

			cutOffDate: '', // 截止日期
			searchShow: false,
			currentTab: 0,
			scrollTop: 0,

			page: 1,
			keywords: '',
			rankField: 'week_hot',
			sign: 0,
			rankList: [],
		};
	},

	onLoad() {
		this.getSunday()
	},
	onShow() {
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 0,
		})
		this.page = 1
		this.keywords = ''
		this.getRankList()
	},
	onHide() {
		this.scrollTop = 1
	},
	/**
	 * 下拉刷新
	 */
	onPullDownRefresh() {
		this.page = 1
		this.keywords = ''
		this.getRankList()
	},
	/**
	 * 上拉加载
	 */
	onReachBottom() {
		this.page++
		this.getRankList()

	},
	methods: {
		getSunday() {
			const time = new Date()
			time.setDate(time.getDate() - time.getDay() + 7)
			this.cutOffDate = (time.getMonth() + 1) + '月' + time.getDate() + '日'
		},
		/**
		 * 去圈子打榜
		 */
		goGroup(starid) {
			if (this.$app.getData('userStar')['id'] == starid) {
				this.$app.goPage('/pages/group/group')
			} else {
				this.$app.goPage('/pages/star/star?starid=' + starid)
			}
		},
		/**
		 * 其他榜单（韩星榜）
		 */
		changeSign(sign) {
			this.page = 1
			this.sign = sign
			this.keywords = ''
			this.getRankList()
		},
		searchInput(e) {
			if (!this.keywords || !e.detail.value) this.rankList = []
			this.page = 1
			this.sign = 0
			this.keywords = e.detail.value
			this.getRankList()
		},
		searchToggle() {
			this.searchShow = !this.searchShow
			if (this.keywords) {
				this.keywords = ''
				this.page = 1
				this.sign = 0
				this.getRankList()
			}


		},

		// HTTP
		getRankList() {
			this.showBottomLoading = true
			this.$app.request(this.$app.API.STAR_RANK, {
				page: this.page,
				rankField: this.rankField,
				keywords: this.keywords,
				sign: this.sign,
			}, res => {
				if (res.data.length < 10) {
					// 内容不足隐藏loadicon
					this.showBottomLoading = false
				}

				const rankList = []
				res.data.forEach(e => {
					const item = {}
					item.starid = e.star.id
					item.name = e.star.name
					item.avatar = e.star.head_img_s ? e.star.head_img_s : e.star.head_img_l
					item.hot = this.$app.formatNumberRgx(e[this.rankField])
					rankList.push(item)
				})
				if (this.page == 1) {
					this.rankList = rankList
				} else {
					// 追加data
					this.rankList = this.rankList.concat(rankList)
				}

				this.$app.closeLoading(this)
			})

		},
	}
}
